<template>
    <div>
    <img :src="원룸.image" :alt=i class="room-img">
    <!-- @click="모달창열렸니" = true; "누른거 = i" -->
    <h4 @click="send">{{ 원룸.title }}</h4>
    <p>{{ 원룸.price }}원</p>
  </div>
</template>

<script>
export default {
    name : 'CardVue',
    props : {
        원룸 : Object, 
        // 모달창열렸니 : Boolean,
    },
    methods : {
        send(){
            this.$emit('openModal' , this.원룸.id)
        }
    }
}
</script>

