<template>
    <div class="discount">
  <h4>지금 결제하면 {{showB}}% 할인</h4>
</div>
</template>

<script>
export default {
    name: 'DiscountComponent',
  data(){
    return{
      showB : 30,
    }
    },
    
    mounted(){
      setInterval(() => {
        this.showB--;
     
      }, 1000);
    }


  }
</script>

