<template>
  <div class="black-bg" v-if="모달창열렸니 == true">
    <div class="white-bg">
      <h4>{{원룸들[누른거].title}}</h4>
      <img style="width: 100%;" :src=원룸들[누른거].image :alt=누른거>
      <p>{{ 원룸들[누른거].content }}</p>
      <!-- <textarea v-model="month"></textarea> -->
      <!-- <select v-model="month">
        <option>1</option>
        <option>2</option>
        <option>3</option>
      </select> -->
      <!-- <input type="checkbox" v-model="month"> -->
      <!-- <input @input="month = $event.target.value"> -->
      <input v-model="month">
      <!-- <input type="range" min="1" max="12"> -->

      <p>{{ month }}개월선택함 : {{ 원룸들[누른거].price * month }}원</p>
      <!-- <button @click="모달창열렸니 = false">닫기</button> -->
      <button @click="close">닫기</button>
    </div>
  </div>
</template>

<script>
export default {
    name : 'ModalWindow',
    data(){
      return {
        month : 1,
      }
    },
    
    beforeUpdate(){
      if(this.month == 2){
        alert('2개월 이상 안팜')
        this.month = 3
      }
    },  

    watch : {
      month(a){
        if(isNaN(a) == true){
          alert('숫자만입력하라')
          this.month = 1
        }

        if (a >= 13){
          alert('13이상 입력하지 마셈')  
        }       
      },
    },

    props : {
      원룸들 : Array, 
      누른거 : Number,
      모달창열렸니 : Boolean,  
    },
    methods : {
      close(){
        this.$emit('closeModal')
      } 
    }
}
</script>

